import { isSameDay } from 'date-fns'
import { format, formatWithOptions } from 'date-fns/fp'
import csLocale from 'date-fns/locale/cs'
import numeral from 'numeral'

import { GAddress } from '../graph/types'

export const formatLocale = formatWithOptions({ locale: csLocale })

export const fullDateFormat = 'dddd LL'
export const formatFullDate = formatLocale(fullDateFormat)

export const dateFormat = 'D.M.YYYY'
export const formatDate = formatLocale(dateFormat)

export const dateFormatISO = 'YYYY-MM-DD'
export const formatDateISO = format(dateFormatISO)

export const timeFormat = 'H:mm'
export const formatTime = formatLocale(timeFormat)

export const dateTimeFormat = 'D.M.YYYY H:mm'
export const formatDateTime = formatLocale(dateTimeFormat)

export const priceFormat = '0,0.[00]'
export const formatPrice = price => numeral(price).format(priceFormat)

export const formatTimeWithoutTodayDate = (time: string) =>
  isSameDay(time, new Date()) ? formatTime(time) : formatDateTime(time)

export const formatAddress = (address: GAddress.Fragment) => {
  if (!address.street) {
    if (address.city) {
      return `${address.city} ${address.houseNumber}, ${address.state}`
    }
    return `${address.state}`
  }
  if (!address.houseNumber) {
    return `${address.street}, ${address.city}`
  }
  return `${address.street} ${address.houseNumber}, ${address.city}`
}
