import { GraphQLError } from 'graphql'
import Raven from 'raven-js'
import { ErrorInfo } from 'react'
import { VError } from 'verror'

import { appConfig } from '../config'
import { logSentry } from './logger'

export const installSentry = (cb: () => void) => {
  const { sentryDSN } = appConfig
  if (sentryDSN) {
    logSentry('enabled with DSN %s', sentryDSN)
    Raven.config(sentryDSN, {
      environment: process.env.REACT_APP_STAGE,
    }).install()
    Raven.context(cb)
  } else {
    cb()
  }
}

export const identifyUser = (userId: string | null) => {
  Raven.setUserContext({
    userId,
  })
}

export const reportRenderingError = (error: Error, info: ErrorInfo) => {
  if (Raven.isSetup()) {
    Raven.captureException(error, {
      extra: info,
    })
  }
}

export const reportFailingQuery = (
  errors: ReadonlyArray<GraphQLError>,
  data: object,
) => {
  errors.forEach(err => {
    Raven.captureMessage(err.message, { extra: err })
  })
  Raven.captureException(new Error('Failed running a query'), { extra: data })
}

export const reportError = (error: Error) => {
  let extra = {}
  if (error instanceof VError) {
    extra = {
      cause: VError.cause(error),
      ...VError.info(error),
    }
  }
  Raven.captureException(error, { extra })
  return error
}
