import { ErrorResponse, onError } from 'apollo-link-error'

import { reportFailingQuery } from '../helpers/sentry'

export const errorHandler = ({ graphQLErrors, operation }: ErrorResponse) => {
  if (graphQLErrors) {
    reportFailingQuery(graphQLErrors, {
      query: operation.operationName,
      variables: operation.variables,
    })
  }
}

export const createErrorLink = () => onError(errorHandler)

export interface IQueryError extends Error {
  showToUser: boolean
}

export type TQueryErrors = IQueryError[] | null

// tslint:disable-next-line:no-any
export const getQueryErrors = async (query: Promise<any>) => {
  const result = await query
  if (result && result.errors) {
    return result.errors as IQueryError[]
  }
  return null
}
