import { types } from 'mobx-state-tree'

import { GCreateFinancialReport } from '../../../graph/types'
import { BaseModel } from '../../../models/BaseModel'
import { CreateFinancialReport } from '../organisms/ClosureStatisticsControl.graphql'

export interface IStatisticsDateFilter {
  dateFrom: Date
  dateTo: Date
}

export const StatisticsModel = BaseModel.named('Statistics')
  .props({
    branches: types.optional(types.array(types.string), []),
    closureBranch: types.maybeNull(types.string),
  })
  .volatile<IStatisticsDateFilter>(self => ({
    dateFrom: new Date(),
    dateTo: new Date(),
  }))
  .views(self => ({
    get branchesFilter() {
      return self.branches.length > 0
        ? Array.from(self.branches)
        : self.root.user.branchesId
    },
    get hasBranchFilter() {
      return self.branches.length > 0
    },
  }))
  .actions(self => ({
    updateFilter(formValues: IStatisticsDateFilter) {
      self.dateFrom = new Date(formValues.dateFrom)
      self.dateTo = new Date(formValues.dateTo)
    },
    clearBranchFilter() {
      self.branches.clear()
    },
    setBranchFilter(branches: ID[]) {
      self.branches.replace(branches)
    },
    setClosureBranch(branch: MaybeID) {
      self.closureBranch = branch
    },
    async createClosure() {
      if (self.closureBranch) {
        const variables: GCreateFinancialReport.Variables = {
          companyBranchId: self.closureBranch,
        }
        return self.client.mutate({
          mutation: CreateFinancialReport,
          variables,
          refetchQueries: [`GClosureReportQuery`],
        })
      } else {
        throw new Error('Tried to create a closure without branch selected')
      }
    },
  }))

export type TStatisticsModel = typeof StatisticsModel.Type
